.contact{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 80px;
    margin: 80px 170px;
}
.contact-title{
    position: relative;
}
.contact-title h1{
    padding: 0px 30px;
    font-size: 80px;
    font-weight: 600;
}
.contact-title img{
    position: absolute;
    bottom: 0;
    right: 0;
    z-index: -1;
}
.contact-section{
    display: flex;
    gap: 150px;
}
.contact-left{
    display: flex;
    flex-direction: column;
    gap: 30px;
}
.contact-left h1{
   font-size: 80px;
   font-weight: 700;
   background: linear-gradient(270deg, #df8908 80%, #b415ff 100%);
   background-clip: text;
   -webkit-background-clip: text;
   -webkit-text-fill-color: transparent;
}
.contact-left p{
  max-width: 550px;
  color: #d4d4d4;
  line-height: 35px;
  font-size: 20px;
}
.contact-details{
    display: flex;
    flex-direction: column;
    gap: 30px;
    color: #d8d8d8;
    font-size: 22px;
}
.contact-detail {
    display: flex;
    align-items: center;
    gap: 20px;
}
.contact-right form{
    display: flex;
    flex-direction: column;
    align-items: start;
    gap: 30px;
}
.contact-right form label{
    color: #d8d8d8;
    font-size: 22px;
    font-weight: 500;
}
.contact-right form input{
    border: none;
    width: 600px;
    height: 70px;
    padding-left: 20px;
    border-radius: 4px;
    background: #32323c;
    color: #a0a0a0;
    font-family: Outfit;
    font-size: 20px;
}
.contact-right form textarea{
    border: none;
    width: 500px;
    height: 100px;
    padding: 20px;
    border-radius: 4px;
    background: #32323c;
    color: #a0a0a0;
    font-family: Outfit;
    font-size: 20px;
}
.contact-submit{
    border: none;
    color: white;
    border-radius: 50px;
    background: linear-gradient(264deg, #df8908 -50.9%, #b415ff 100%);
    font-size: 22px;
    padding: 20px 50px;
    margin-bottom: 50px;
    cursor: pointer;
    transition: 0.3s;
}
.contact-submit:hover{
   transform: scale(1.1);
   transition: 0.3s;
}


@media(max-width : 768px){
    .contact{
        gap:  50px;
        margin: 50px 70px;
        align-items: start;
    }
    .contact-title h1{
        padding: 0;
        font-size: 50px;
    }
    .contact-title img{
        width: 130px;
        right: 0;
    }
    .contact-left h1{
        font-size: 50px;
     }
     .contact-section{
        flex-direction: column;
     }
     .contact-left p{
        font-size: 18px;
     }
     .contact-detail{
        gap: 20px;
     }
     .contact-detail img {
        width: 25px;
     }
     .contact-right label{
        font-size: 20px;
     }
     .contact-right input{
        width: 100%;
        font-size: 20px;
     }
     .contact-right textarea{
        width: 95%;
        font-size: 20px;
     }
     .contact-submit{
        font-size: 20px;
        padding: 15px 40px;
     }
}