.services{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 80px;
    margin: 80px 170px;
}
.services-title{
    position: relative;
}
.services-title h1{
    padding: 0px 30px;
    font-size: 80px;
    font-weight: 600;
    
}
.services-title img{
   position: absolute;
   bottom: 0;
   right: 0;
   z-index: -1;

}
.services-container{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 40px;
    margin-bottom: 80px;
}
.services-card{
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 20px;
    padding: 60px;
    border-radius: 10px;
    border: 2px solid white;
    transition: 0.4s;
    cursor: pointer;
}
.services-card:hover{
    border: 2px solid #ff00ff;
    background-image: linear-gradient(45deg, #3f0028, #582300);
    transform: scale(1.05);
    transition: 0.4s;
}
.services-card h3{
   font-size: 28px;
   font-weight: 600;
}
.services-card h2{
   font-size: 30px;
   font-weight: 800;
   background: linear-gradient(270deg, #df8908 50%, #b415ff 100%);
   background-clip: text;
   -webkit-background-clip: text;
   -webkit-text-fill-color: transparent;
}
.services-card p{
  color: #d4d4d4;
  font-size: 24px;
  font-weight: 500;
  line-height: 40px;
  max-width: 300px;
}
.services-readMore{
    display: flex;
    gap: 20px;
    align-items: center;
    margin-top: 10px;
}



@media (max-width : 768px){
    .services{
        align-items: start;
        gap: 70px;
        margin: 20px 70px;
    }
    .services-title h1{
        padding: 0;
        font-size: 50px;
    }
    .services-title img{
        width: 130px;
        right: 0;
    }
    .services-card{
        gap: 20px;
        padding: 32px;
    }
    .services-card h2{
        font-size: 28px;
    }
    .services-card p{
        font-size: 18px;
        line-height: 30px;
        max-width: 400px;
    }
    .services-readMore{
        font-size: 20px;
    }
    .services-container{
        grid-template-columns: 1fr 1fr;
        gap: 30px;
    }
}